.main{
    flex: 1;
    min-height: 100vh;
}

.main img{
    width: 25px;
}

.main .top-bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.main .top-bar p{
    font-size: 22px;
    color: #585858;
}

.main .top-bar img{
    cursor: pointer;
}

.main .top-bar .user-icon{
    width: 40px;
    border-radius: 50%;
}

.main-container{
    max-width: 850px;
    margin: auto;
}

.main .welcome-bar{
    margin-top: 50px;
    padding: 20px 0px;
}

.main .welcome-bar p{
    font-size: 56px;
    font-weight: 500;
}

.main .welcome-bar span{
    background: linear-gradient(100deg,#4b90ff,#ff5546); 
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.main .welcome-bar .head2{
    color: #c4c7c5;
}

.main .cards-bar{
    margin-top: 50px;
    padding: 20px 0px;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(180px,1fr));
    gap: 15px;
}

.main .cards-bar .card{
    background-color: #f0f4f9;
    height: 200px;
    padding: 15px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}

.main .cards-bar .card:hover{
    cursor: pointer;
    background-color: rgb(216,219,224);
}

.main .cards-bar .card .card-desc{
    font-size: 17px;
    color: #585858;
}

.main .cards-bar .card .card-icon{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.main .message-bar{
    margin-top: 30px;
    position: relative;
}

.main .message-bar input{
    width: 100%;
    padding: 10px;
    border: 0;
    background-color: #f0f4f9;
    border-radius: 30px;
    padding: 15px 25px;
    font-size: 18px;
}

.main .message-bar input:focus{
    outline: none;
}

.main .message-bar .img-box{
    position: absolute;
    right: 0;
    top: 0;
    padding: 15px 25px;
    display: flex;
    gap: 10px;
}

.main .message-bar .img-box img{
    cursor: pointer;
}

.main .bottom-bar{
    margin-top: 15px;
    font-size: 13px;
    font-weight: 300;
    text-align: center;
}

.result{
    max-height: 70vh;
    padding: 0px 2%;
    overflow-y: scroll;
}

.result::-webkit-scrollbar{
    display: none;
}

.result img{
    width: 40px;
    border-radius: 50%;
}

.prompt-title{
    margin: 40px 0px;
    display: flex;
    align-items: center;
    gap: 15px;
}

.output-data{
    display: flex;
    align-items: flex-start;
    gap: 15px;
    font-size: 17px;
    font-weight: 300;
    line-height: 2;
}

.loader{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.loader hr{
    border-radius: 5px;
    border: none;
    background-color: #f6f7f8;
    background: linear-gradient(to right , #9ed7ff, #ffffff , #9ed7ff);
    background-size: 800px 50px;
    height: 20px;
    animation: loader 3s infinite linear;
}

@keyframes loader {
    0%{
        background-position: -800px 0px;
    }
    100%{
        background-position: 800px 0px;
    }
}

@media (max-width:600px) {
    .main-container{
        padding: 0px 20px;
    }
    .main .message-bar input{
        padding: 12px 15px;
        font-size: 16px;
    }
    .main .message-bar .img-box{
        gap: 5px;
        padding: 12px 15px;
    }
    .main .message-bar .img-box img{
        width: 20px;
    }
    .main .bottom-bar{
        font-size: 11px;
    }
    .output-data{
        font-size: 15px;
    }
}