@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Outfit;
}

#root{
  min-height: 100vh;
  display: flex;
  animation: fading 1.5s;
}

@keyframes fading {
    0%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
}