.sidebar{
    background-color: #f0f4f9;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 25px 15px;
}

.sidebar img{
    width: 20px;
}

.sidebar .menu{
    display: block;
    margin-left: 10px;
    cursor: pointer;
}

.sidebar .new-chat{
    margin-top: 50px;
    display: inline-flex;
    background-color: #e6eaf1;
    align-items: center;
    gap : 10px;
    padding: 10px 15px;
    border-radius: 30px;
    font-size: 14px;
    color: gray;
    cursor: pointer;
}

.sidebar .recent{
    display: flex;
    flex-direction: column;
    animation: fading 1.5s;
}

.sidebar .recent-title{
    margin-top: 30px;
    margin-bottom: 20px;
    font-size: 16px;
}

.sidebar .recent-entry{
    display: flex;
    align-items: start;
    gap : 10px;
    padding: 10px;
    padding-right: 40px;
    border-radius: 30px;
    font-size: 16px;
    color: #282828;
}

.sidebar .recent-entry:hover{
    background-color: #e6eaf1;
    cursor: pointer;
}

.sidebar .lower{
    display: flex;
    flex-direction: column;
}

.sidebar .bottom-item{
    padding-right: 10px;
}

@media (max-width:600px) {
    .sidebar{
        display: none;
    }
}